import { __spreadArray } from "tslib";
import FlashCpu from "./animations/FlashCpu";
import FlashRam from "./animations/FlashRam";
import FlashWire from "./animations/FlashWire";
import StoreCpuState from "./state/StoreCpuState";
import CompareUpdateSW from "./cpu/CompareUpdateSW";
import ExecuteAluOperation from "./cpu/ExecuteAluOperation";
import UpdateSW from "./cpu/UpdateSW";
import StoreAccToAddress from "./ram/StoreAccToAddress";
import HaltExecution from "./cpu/HaltExecution";
import { DECODE_OPCODE, SET_MUX, SET_ALU_OPERATION, LOAD_ALU1_FROM_ACC, LOAD_ALU2, SET_PC_TO_IR_OPERAND, SET_PC_TO_IR_OPERAND_IF_ZERO_FLAG, SET_PC_TO_IR_OPERAND_IF_NOT_ZERO_FLAG, SET_PC_TO_IR_OPERAND_IF_NEGATIVE_FLAG, SET_PC_TO_IR_OPERAND_IF_NOT_NEGATIVE_FLAG } from "./presets";
import { TTS_FINISHED } from "./Waits";
import Parallel from "./macro/Parallel";
import ReadStep from "./tts/ReadStep";
export function instructionToActions(instruction) {
    if (!instruction.opcode) {
        throw new Error("Invalid opcode");
    }
    var actions = [];
    switch (instruction.opcode.symbolic) {
        case "ADD":
        case "SUB":
        case "MUL":
        case "DIV":
        case "AND":
        case "NOT":
            actions.push.apply(actions, __spreadArray(__spreadArray(__spreadArray(__spreadArray(__spreadArray(__spreadArray([], DECODE_OPCODE, false), SET_MUX, false), SET_ALU_OPERATION, false), LOAD_ALU1_FROM_ACC, false), LOAD_ALU2(instruction.immediateFlag()), false), [new Parallel(new FlashWire("ALU:4", "ACC:2"), new ReadStep("execute")
                // new ShowText("execute")
                ),
                new ExecuteAluOperation().thenWaitFor(TTS_FINISHED).endstep(),
                new Parallel(new FlashWire("ALU:3", "SW:1"), new ReadStep("alu_to_sw")
                // new ShowText("alu_to_sw")
                ),
                new UpdateSW().thenWaitFor(TTS_FINISHED).endstep()], false));
            break;
        case "NOT":
            actions.push.apply(actions, __spreadArray(__spreadArray(__spreadArray(__spreadArray(__spreadArray([], DECODE_OPCODE, false), SET_MUX, false), SET_ALU_OPERATION, false), LOAD_ALU2(instruction.immediateFlag()), false), [new Parallel(new FlashWire("ALU:4", "ACC:2"), new ReadStep("execute")
                // new ShowText("execute")
                ),
                new ExecuteAluOperation().thenWaitFor(TTS_FINISHED).endstep(),
                new Parallel(new FlashWire("ALU:3", "SW:1"), new ReadStep("alu_to_sw")
                // new ShowText("alu_to_sw")
                ),
                new UpdateSW().thenWaitFor(TTS_FINISHED).endstep()], false));
            break;
        case "CMP":
            actions.push.apply(actions, __spreadArray(__spreadArray(__spreadArray(__spreadArray(__spreadArray(__spreadArray([], DECODE_OPCODE, false), SET_MUX, false), SET_ALU_OPERATION, false), LOAD_ALU1_FROM_ACC, false), LOAD_ALU2(instruction.immediateFlag()), false), [new Parallel(new FlashWire("ALU:3", "SW:1"), new ReadStep("alu_to_sw")
                // new ShowText("alu_to_sw")
                ),
                new CompareUpdateSW().thenWaitFor(TTS_FINISHED).endstep()], false));
            break;
        case "LOD":
            actions.push.apply(actions, __spreadArray(__spreadArray(__spreadArray(__spreadArray(__spreadArray([], DECODE_OPCODE, false), SET_MUX, false), SET_ALU_OPERATION, false), LOAD_ALU2(instruction.immediateFlag()), false), [new Parallel(new FlashWire("ALU:4", "ACC:2"), new ReadStep("execute")
                // new ShowText("execute")
                ),
                new ExecuteAluOperation().thenWaitFor(TTS_FINISHED).endstep()], false));
            break;
        case "STO":
            actions.push.apply(actions, __spreadArray(__spreadArray([], DECODE_OPCODE, false), [new Parallel(new FlashWire("IR:2", "RAM:ADD"), new ReadStep("ir_to_ram")
                // new ShowText("ir_to_ram")
                ),
                new FlashRam("ADDRESS", "IR:OPR").thenWaitFor(TTS_FINISHED).endstep(),
                new Parallel(new StoreCpuState("ACC"), new FlashCpu("ACC"), new ReadStep("acc_to_ram")
                // new ShowText("acc_to_ram")
                ),
                new FlashWire("ACC:1", "RAM:DATA").thenWaitFor(TTS_FINISHED).endstep(),
                new Parallel(new FlashWire("CU:3", "RAM:CTRL"), new ReadStep("memory_write")
                // new ShowText("memory_write")
                )
                    .thenWaitFor(TTS_FINISHED)
                    .endstep(),
                new Parallel(new StoreAccToAddress("IR:OPR"), new ReadStep("acc_stored_to_ram")
                // new ShowText("acc_stored_to_ram")
                )
                    .thenWaitFor(TTS_FINISHED)
                    .endstep()], false));
            break;
        case "JMP":
            actions.push.apply(actions, __spreadArray(__spreadArray([], DECODE_OPCODE, false), SET_PC_TO_IR_OPERAND, false));
            break;
        case "JZ":
            actions.push.apply(actions, __spreadArray(__spreadArray(__spreadArray([], DECODE_OPCODE, false), [new FlashCpu("SW:Z")], false), SET_PC_TO_IR_OPERAND_IF_ZERO_FLAG, false));
            break;
        case "JNZ":
            actions.push.apply(actions, __spreadArray(__spreadArray(__spreadArray([], DECODE_OPCODE, false), [new FlashCpu("SW:Z")], false), SET_PC_TO_IR_OPERAND_IF_NOT_ZERO_FLAG, false));
            break;
        case "JN":
            actions.push.apply(actions, __spreadArray(__spreadArray(__spreadArray([], DECODE_OPCODE, false), [new FlashCpu("SW:N")], false), SET_PC_TO_IR_OPERAND_IF_NEGATIVE_FLAG, false));
            break;
        case "JNN":
            actions.push.apply(actions, __spreadArray(__spreadArray(__spreadArray([], DECODE_OPCODE, false), [new FlashCpu("SW:N")], false), SET_PC_TO_IR_OPERAND_IF_NOT_NEGATIVE_FLAG, false));
            break;
        case "NOP":
            actions.push.apply(actions, DECODE_OPCODE);
            break;
        case "HLT":
            actions.push.apply(actions, __spreadArray(__spreadArray([], DECODE_OPCODE, false), [new HaltExecution()], false));
            break;
        default:
            throw new Error("Unrecognized opcode: \"".concat(instruction.opcode["symbolic"], "\""));
    }
    return actions;
}
