import { get } from "svelte/store";
import { playAnimations, textToSpeech } from "../../store/settings";
import state from "../state";
export function ZERO_FLAG_SET() {
    return state["SW:Z"];
}
export function ZERO_FLAG_NOT_SET() {
    return !state["SW:Z"];
}
export function NEGATIVE_FLAG_SET() {
    return state["SW:N"];
}
export function NEGATIVE_FLAG_NOT_SET() {
    return !state["SW:N"];
}
export function IS_ANIMATING() {
    return get(playAnimations);
}
export function TTS_ENABLED() {
    return get(textToSpeech);
}
